.card {
  display: flex;
  padding: 20px;
  margin: 10px;
  color: white;
  background-color: #372b21;
  width: auto;
  height: auto;
  border-radius: 25px;
}

.center {
  text-align: center;
  display: block;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 100%;
}

.image {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  align-self: center;
}

#name {
  font-family: Verdana;
  font-size: 25px;
  color: white;
  padding-left: 10px;
}

#nim {
  font-family: Verdana;
  font-size: 16px;
  color: whitesmoke;
  padding: 10px;
}

.igbtn {
  height: 35px;
  width: 100px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  background-color: #8a3ab9;
  color: white;
}

.githubbtn {
  margin-left: 20px;
  height: 35px;
  width: 100px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 30px;
  background-color: #000000;
  color: white;
}
